	display: block;
	width: 100%;
	cursor: pointer;
}

.portfolio-grid .grid-item {
	opacity: 1;
	overflow: hidden;
}

.portfolio-grid .grid-item figure img {
	cursor: pointer;
	display: block;
	opacity: 1;
	width: 100%;
	transition: all 0.3s ease 0s;
}

.portfolio-grid .grid-item figcaption {
	bottom: 0;
	left: 0;
	opacity: 0;
