/* ===================================
    Swiper carousel
====================================== */

.swiper-container {
	overflow: hidden;
	position: relative
}

.swiper-pagination-bullet-active {
	background: #232323
}

.swiper-pagination {
	width: 100%
}

.swiper-container-horizontal > .swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction {
	bottom: 35px;
	width: 100%
}

.swiper-pagination-bullet {
	height: 9px;
	width: 9px
}

.swiper-pagination-bullets .swiper-pagination-bullet {
	margin: 0 5px
}

.swiper-pagination-bottom .swiper-wrapper {
	margin: 0 0 35px 0
}

.swiper-pagination-bottom.swiper-container-horizontal .swiper-wrapper {
	margin: 0 0 95px 0
}

.swiper-slider-second {
	text-align: center;
}

/* swiper bottom scrollbar full */
.swiper-bottom-scrollbar-full.swiper-container {
	height: 100%;
	margin: 0 auto;
	overflow: hidden;
	position: absolute;
	top: 0;
	transition: all 0.3s ease-in-out 0.7s;
	-webkit-transition: all 0.3s ease-in-out 0.7s;
	-moz-transition: all 0.3s ease-in-out 0.7s;
	-ms-transition: all 0.3s ease-in-out 0.7s;
	-o-transition: all 0.3s ease-in-out 0.7s;
	width: 100%;
}

.swiper-bottom-scrollbar-full .swiper-scrollbar {
	left: 0;
	opacity: 1;
	position: absolute;
	width: 100%;
	bottom: 0;
	border-radius: 0;
	height: 2px
}

.swiper-bottom-scrollbar-full .swiper-scrollbar-drag {
	border-radius: 0;
	background: $primary
}

.swiper-bottom-scrollbar-full .swiper-scrollbar {
	background: rgba(255, 255, 255, .2)
}

.swiper-bottom-scrollbar-full .swiper-slide img {
	max-height: 450px;
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	opacity: .8;
	-webkit-filter: grayscale(1);
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-ms-filter: grayscale(1);
	-o-filter: grayscale(1);
}

.swiper-bottom-scrollbar-full .swiper-slide:hover img {
	opacity: 1;
	-webkit-filter: grayscale(0);
	-moz-filter: grayscale(0);
	-ms-filter: grayscale(0);
	-o-filter: grayscale(0);
	filter: grayscale(0);
	filter: contrast(130%);
	-webkit-filter: contrast(130%);
	-moz-filter: contrast(130%);
	-ms-filter: contrast(130%);
	-o-filter: contrast(130%);
	max-height: 500px;
}

.swiper-bottom-scrollbar-full .swiper-slide .hover-title-box {
	visibility: hidden;
}

.swiper-bottom-scrollbar-full .swiper-slide:hover .hover-title-box {
	right: -160px;
	opacity: 1;
	visibility: visible;
}

.swiper-bottom-scrollbar-full .bottom-text {
	position: absolute;
	bottom: 50px;
	opacity: 0;
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
}

.swiper-bottom-scrollbar-full .swiper-slide:hover .bottom-text {
	opacity: .7;
	letter-spacing: 5px;
	left: 4px;
}

/* swiper vertical pagination */
.swiper-vertical-pagination {
	width: 100%;
	height: 100%;
	position: absolute;
}

.swiper-vertical-pagination .swiper-slide {
	text-align: center;
	background: $white;
	display: -webkit-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	-webkit-box-align: center;
	-ms-flex-align: center;
	-webkit-align-items: center;
	align-items: center;
}

.swiper-vertical-pagination > .swiper-pagination-bullets {
	right: 60px;
}

.swiper-vertical-pagination .swiper-pagination {
	width: auto
}

.swiper-vertical-pagination .swiper-slide .swiper-bottom-content {
	opacity: 0;
	transform: translate3d(0px, 150px, 0px);
	-webkit-transform: translate3d(0px, 150px, 0px);
	-moz-transform: translate3d(0px, 150px, 0px);
	-ms-transform: translate3d(0px, 150px, 0px);
	-o-transform: translate3d(0px, 150px, 0px);
	transition: all 0.3s ease-in-out 0s;
	-webkit-transition: all 0.3s ease-in-out 0s;
	-moz-transition: all 0.3s ease-in-out 0s;
	-ms-transition: all 0.3s ease-in-out 0s;
	-o-transition: all 0.3s ease-in-out 0s;
}

.swiper-vertical-pagination .swiper-slide-active .swiper-bottom-content {
	opacity: 1;
	transform: translate3d(0px, 0px, 0px);
	-webkit-transform: translate3d(0px, 0px, 0px);
	-moz-transform: translate3d(0px, 0px, 0px);
	-ms-transform: translate3d(0px, 0px, 0px);
	-o-transform: translate3d(0px, 0px, 0px);
	transition: all 0.3s ease-in-out 0.3s;
	-webkit-transition: all 0.3s ease-in-out 0.3s;
	-moz-transition: all 0.3s ease-in-out 0.3s;
	-ms-transition: all 0.3s ease-in-out 0.3s;
	-o-transition: all 0.3s ease-in-out 0.3s;
}

.swiper-vertical-pagination .box-arrow {
	position: absolute;
	right: -45px;
	width: 90px;
	height: 90px;
}

/* swiper auto width */
.swiper-auto-width .swiper-slide {
	width: 60%;
}

.swiper-auto-width.swiper-container {
	height: calc(100% - 200px);
	top: 100px;
	margin: 0 auto;
	overflow: visible;
	position: absolute;
	transform: scale(1);
	-webkit-transform: scale(1);
	-moz-transform: scale(1);
	-ms-transform: scale(1);
	-o-transform: scale(1);
	transition: all 0.3s ease-in-out 0.7s;
	-webkit-transition: all 0.3s ease-in-out 0.7s;
	-moz-transition: all 0.3s ease-in-out 0.7s;
	-ms-transition: all 0.3s ease-in-out 0.7s;
	-o-transition: all 0.3s ease-in-out 0.7s;
	width: 100%;
}

.swiper-auto-width .swiper-scrollbar {
	left: 20%;
	opacity: 1;
	position: absolute;
	width: 60%;
	bottom: -50px;
	border-radius: 0;
	height: 1px
}

.swiper-auto-width .swiper-scrollbar-drag {
	border-radius: 0;
	background: transparent;
}

.swiper-auto-width .swiper-scrollbar-drag:before {
	
	position: absolute;
	content: "";
	background: $primary;
	width: 13px;
	height: 13px;
	border-radius: 100%;
	top: -6px;
	left: calc(50% - 13px);
}

.swiper-auto-width .swiper-slide {
	filter: contrast(130%);
	-webkit-filter: contrast(130%);
	-moz-filter: contrast(130%);
	-ms-filter: contrast(130%);
	-o-filter: contrast(130%);
	filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-ms-filter: grayscale(1);
	-o-filter: grayscale(1);
	transition: all 0.3s ease-in-out 0s;
	-webkit-transition: all 0.3s ease-in-out 0s;
	-moz-transition: all 0.3s ease-in-out 0s;
	-ms-transition: all 0.3s ease-in-out 0s;
	-o-transition: all 0.3s ease-in-out 0s;
}

.swiper-auto-width .swiper-slide.swiper-slide-active {
	filter: contrast(0);
	-webkit-filter: contrast(0);
	-moz-filter: contrast(0);
	-ms-filter: contrast(0);
	-o-filter: contrast(0);
	filter: grayscale(0);
	-webkit-filter: grayscale(0);
	-moz-filter: grayscale(0);
	-ms-filter: grayscale(0);
	-o-filter: grayscale(0);
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
}

.swiper-auto-width .swiper-slide .absolute-middle-center h2 {
	opacity: 0;
	margin-bottom: 10px;
	transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transform: translateY(-60px);
	-webkit-transform: translateY(-60px);
	-moz-transform: translateY(-60px);
	-ms-transform: translateY(-60px);
	-o-transform: translateY(-60px);
}

.swiper-auto-width .swiper-slide.swiper-slide-active .absolute-middle-center h2 {
	opacity: 1;
	transition: all 0.3s ease-in-out 0.7s;
	-webkit-transition: all 0.3s ease-in-out 0.7s;
	-moz-transition: all 0.3s ease-in-out 0.7s;
	-ms-transition: all 0.3s ease-in-out 0.7s;
	-o-transition: all 0.3s ease-in-out 0.7s;
	transform: translateY(0px);
	-webkit-transform: translateY(0px);
	-moz-transform: translateY(0px);
	-ms-transform: translateY(0px);
	-o-transform: translateY(0px);
}

.swiper-auto-width .swiper-slide span {
	opacity: 0;
	transition: all 0.5s ease-in-out;
