	-o-transform: translateY(-50%);
}

.swiper-portfolio-prev.swiper-button-disabled, .swiper-portfolio-next.swiper-button-disabled {
	opacity: 0;
	visibility: hidden;
}

.swiper-portfolio-prev {
	left: 15px;
}

.swiper-portfolio-next {
	right: 15px;
}

/* next prev button style 3 */
.swiper-button-prev.swiper-prev-style3, .swiper-button-next.swiper-next-style3 {
	background-image: none;
	position: absolute;
	cursor: pointer;
	left: 41.1%;
	top: inherit;
	text-align: center;
	font-size: 16px;
	line-height: 40px;
	width: 40px;
	height: 40px;
	background-color: $primary;
	color: $white;
	z-index: 9;
}

.swiper-button-prev.swiper-prev-style3 {
	bottom: 141px
}

.swiper-button-next.swiper-next-style3 {
	bottom: 100px;
}

/* next prev button style 4 */
.navigation-area {
	position: absolute;
	bottom: 0;
	display: inline-block;
	width: 40px;
	height: 82px;
}

.swiper-button-prev.swiper-prev-style4, .swiper-button-next.swiper-next-style4 {
	background-image: none;
	background: $primary;
	color: $white;
	text-align: center;
	line-height: 40px;
	height: 40px;
	width: 40px;
	display: block;
	float: left;
	position: relative;
	left: auto;
	right: auto;
	top: 0;
	margin: 0 auto;
	i {
		line-height: inherit;
	}
}

.swiper-button-next.swiper-next-style4 {
	margin-bottom: 1px
}

/* next prev button style 5 */
.swiper-button-prev.swiper-prev-style5, .swiper-button-next.swiper-next-style5 {
	position: absolute;
	top: 22px;
	bottom: 0;
	height: 100%;
	width: 15%;
	background-image: none;
	transition-duration: 0.3s;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	-ms-transition-duration: 0.3s;
	-o-transition-duration: 0.3s;
}

.swiper-button-prev.swiper-prev-style5 {
	opacity: 0;
	background: -moz-linear-gradient(left, rgba($black, 0.5), transparent);
	background: -webkit-linear-gradient(left, rgba($black, 0.5), transparent);
	background: linear-gradient(to right, rgba($black, 0.5), transparent);
}

.swiper-button-next.swiper-next-style5 {
	opacity: 0;
	background: -moz-linear-gradient(right, rgba($black, 0.5), transparent);
	background: -webkit-linear-gradient(right, rgba($black, 0.5), transparent);
	background: linear-gradient(to left, rgba($black, 0.5), transparent);
}

.swiper-button-prev.swiper-prev-style5:hover, .swiper-button-next.swiper-next-style5:hover {
	opacity: 1;
	transition-duration: 0.3s;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	-ms-transition-duration: 0.3s;
	-o-transition-duration: 0.3s;
}

.swiper-button-prev.swiper-prev-style5 {
	left: 0;
	cursor: url('../images/arrow-prev-light-dark.png'), pointer;
}

.swiper-button-next.swiper-next-style5 {
	right: 0;
	cursor: url('../images/arrow-next-light-dark.png'), pointer;
}

/* dot pagination white */
.swiper-pagination-white .swiper-pagination-bullet {
	background: $white
}

.swiper-pagination-white .swiper-pagination-bullet-active {
	background: $white
}

.swiper-pagination-square-bottom.swiper-pagination-bullets {
	bottom: -50px
}

/* square pagination */
.swiper-pagination-square .swiper-pagination-bullet {
	height: 3px;
	width: 30px;
	border-radius: 0
}

/* square pagination */
.white-move .swiper-slide {
	cursor: pointer;
}

.black-move .swiper-slide {
	cursor: pointer;
}

/* number pagination */
.swiper-number {
	left: auto !important;
	right: 0 !important;
	width: 35px !important;
	text-align: right !important;
	top: 38% !important;
	bottom: inherit !important;
}

.swiper-number .swiper-pagination-bullet {
	background: transparent;
	color: $gray-700;
	display: block;
	font-size: 20px;
	height: 45px;
	line-height: 20px;
	opacity: .7;
	padding: 14px 0 0;
	text-align: center;
	width: 23px;
	border-radius: 0;
	margin: 0 0 5px 0;
}

.swiper-number .swiper-pagination-bullet.swiper-pagination-bullet-active {
	border-bottom: 1px solid rgba($black, .5);
	opacity: 1;
}

.swiper-number .swiper-pagination-bullet-active {
	color: $black;
	background: transparent;
}

/* revolution slider bullet */
.tp-bullet {
	border-radius: 100%;
	background: transparent;
	border: 1px solid $white;
}

.tp-bullet.selected, .tp-bullet:hover {
	background: $white
}

.hermes .tp-bullet {
	box-shadow: 0 0 0 0 $white;
	height: 12px;
	width: 12px;
	border: 2px solid $white;
}

.hermes .tp-bullet::after {
	box-shadow: 0 0 0 0 $white inset;
}

/* slider height */
.slider-half-small-screen {
	min-height: 630px
}

.slider-half-screen {
	min-height: 765px
}

.slider-medium-screen {
	min-height: 898px
}

/* scroll down */
.scroll-down {
	bottom: 0px;
	position: absolute;
	text-align: center;
	width: 100%;
	z-index: 11
}

.scroll-down a {
	background: #232323;
	display: inline-block;
	height: 57px;
	width: 57px;
	text-align: center
}

.scroll-down a i.scroll-arrow-down {
	vertical-align: top;
	background-image: url('../images/scroll-down-icon.png');
	width: 17px;
	height: 30px;
	display: inline-block
}

.scroll-down a:hover {
	background: $white;
}

.scroll-to-down {
	cursor: url("../images/arrow-down.png"), pointer;
}

.scroll-to-down-white {
	cursor: url("../images/arrow-down-white.png"), pointer;
}

.background-slider-text {
	position: absolute;
	width: 100%
}

/* cursor */
.cursor-default {
	cursor: default
}

/* ===================================
    Slider and parallax typography
====================================== */

.slider-text-middle-main {
	display: table;
	height: 100%;
	width: 100%
}

.slider-text-middle {
	display: table-cell;
	vertical-align: middle;
}

.slider-typography {
	width: 100%;
	height: 100%;
	position: absolute;
	right: 0;
	left: 0;
}

.slider-text-bottom {
	display: table-cell;
	vertical-align: bottom;
}

.slider-text-middle {

}

.slider-text-top {
	display: table-cell;
	vertical-align: top;
}

.down-section {
	position: absolute;
	width: 100%;
	bottom: 50px;
	left: 0;
	right: 0
}

.blog-slider .swiper-slide .slider-typography {
	padding: 60px;
}

.swiper-slide {
	&.width-auto {
		width: auto;
	}
	
	&.width-550px {
		width: 550px;
	}
	
	&.width-50 {
		width: 50%;
	}
}


