/* header icon */
.header-searchbar {
	border-left: 1px solid rgba($black, .15);
	padding-left: 15px;
	display: inline-block;
	line-height: 15px;
	position: relative;
	top: -1px;
}

.header-social-icon {
	border-left: 1px solid rgba($black, 0.15);
	padding-left: 15px;
	margin-left: 12px;
	display: inline-block;
	line-height: 15px;
	position: relative;
	top: -1px;
}

.heder-menu-button {
	display: inline-block;
	line-height: 14px;
	padding-left: 5px;
	position: relative;
	top: -1px;
}

nav.navbar.bootsnav .heder-menu-button .navbar-toggle {
	float: none;
	padding: 0;
	margin-top: 0;
	margin-bottom: 0;
	top: 4px
}

.white-link .header-searchbar, .white-link .header-social-icon {
	border-left: 1px solid rgba(255, 255, 255, .15);
}

.header-social-icon a {
	color: #232323
}

.header-social-icon a i, .header-searchbar a i {
	margin: 0 11px;
}

.header-searchbar a i {
	position: relative;
	top: -1px;
}

.header-social-icon a:last-child i {
	margin-right: 0;
}

.search-icon {
	padding-right: 10px;
}

/* menu center */
.menu-center .header-right {
	flex: 1 2 auto;
}

.navbar-collapse {
	padding-left: 0;
	padding-right: 0;
}

/* menu center logo */
.menu-logo-center .navbar-left > li {
	float: none;
	display: inline-block;
	vertical-align: middle;
}

header nav.menu-logo-center .navbar-nav > li > a {
	margin: 0 20px;
}

header nav.menu-logo-center .navbar-nav > li:last-child a {
	margin-right: 20px
}

.menu-logo-center {
	@media (max-width: 991px) {
		.navbar-left.text-right {
			text-align: left;
		}
		
		.logo {
			position: absolute !important;
			left: 0 !important;
			top: -5px !important;
		}
		
		.width-40 {
			width: 100% !important;
		}
		
		.navbar-toggle {
			position: absolute !important;
			right: 25px !important;
			top: 20px !important;
		}
	}
}

