	-moz-transform: translateX(0) scaleX(1);
	-webkit-transform: translateX(0) scaleX(1);
	-o-transform: translateX(0) scaleX(1);
	-ms-transform: translateX(0) scaleX(1);
	opacity: 1;
	z-index: 9999
}

.big-menu-close {
	background: $white;
	position: absolute;
	right: 0;
	width: 40px;
	height: 40px;
	color: #232323;
}

/* dropdown menu */
nav.navbar.navbar-default ul.nav > li > a {
	padding: 26px 0;
	background: transparent;
	border-color: transparent;
	font-weight: 500
}

nav.navbar.navbar-default ul.nav > li.dropdown > a.dropdown-toggle::after, nav.navbar.sidebar-nav ul.nav > li.dropdown > a.dropdown-toggle::after {
	content: "";
	vertical-align: middle;
	position: relative;
	top: -1px;
	margin-top: 0;
}

nav.navbar.navbar-default ul li.dropdown ul.dropdown-menu > li:last-child {
	border-bottom: 0;
	border-bottom: 0;
}

nav.navbar.navbar-default ul li.dropdown .dropdown-menu li > a:hover {
	color: $white;
	background: transparent;
	left: 5px;
}

nav.navbar.navbar-default ul li.dropdown .dropdown-menu li.active > a {
	color: $white;
}

nav.navbar.navbar-default ul.menu-links li.dropdown ul.dropdown-menu li > a:hover, nav.navbar.navbar-default ul.menu-links li.dropdown ul.dropdown-menu li.active > a {
	color: #232323
}

.dropdown ul.second-level {
	left: 270px;
	top: 0;
	height: 100%;
}

.dropdown ul.third-level {
	left: 530px;
	top: 0;
	height: 100%;
}

nav.navbar ul.nav .dropdown-menu.second-level .dropdown-menu {
	margin-top: 0
}

.navbar-default ul li li.dropdown ul {
	display: none
}

.navbar-default ul li li.dropdown.on > ul {
	display: block;
	position: absolute;
	left: 100%;
	margin-top: 0;
	padding: 0;
	top: 0;
}

.navbar-default ul li li.dropdown.on > ul > li.dropdown.on > ul {
	display: block;
	position: absolute;
	left: 100%;
	margin-top: 0;
	padding: 0;
	top: 0;
}

.navbar-default ul li li.dropdown ul.dropdown-menu {
	position: absolute;
	left: 100%;
	margin-top: 0;
	padding: 0;
	top: 0;
	box-shadow: none;
}

.navbar-default ul li li.dropdown ul.dropdown-menu li {
	padding: 0;
}

.dropdown-menu .dropdown a {
	position: relative
}

/* navigation left sidebar */
header .menu-wrap .widget {
	margin-bottom: 30px;
	display: inline-block;
	width: 100%
}

.nav.navbar-left-sidebar {
	transition: all 0s ease-in-out 0s;
	-webkit-transition: all 0s ease-in-out 0s;
	-moz-transition: all 0s ease-in-out 0s;
	-ms-transition: all 0s ease-in-out 0s;
	-o-transition: all 0s ease-in-out 0s;
	overflow-y: auto;
}

.nav.navbar-left-sidebar li {
	display: inline-block;
	width: 100%;
	padding: 0 45px;
}

.nav.navbar-left-sidebar li a, nav.navbar.bootsnav.navbar-left-sidebar ul.nav > li > a {
	font-size: 11px;
	text-transform: uppercase;
	color: $gray-700;
	background: transparent !important;
	border-bottom: 1px solid #e5e5e5 !important;
	padding: 15px 0
}

.nav.navbar-left-sidebar li:last-child a {
	border-bottom: none
}

.nav.navbar-left-sidebar li a:hover {
	color: $white
}

.nav.navbar-left-sidebar li a .nav-caret {
	height: 35px;
	text-align: right;
	width: 35px;
}

.nav.navbar-left-sidebar .dropdown .second-level {
	opacity: 0 !important;
	left: 245px;
	display: block !important;
	visibility: hidden;
}

.nav.navbar-left-sidebar .dropdown.on .second-level {
	opacity: 1 !important;
	left: 289px;
	visibility: visible;
	transition-duration: 0.3s;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	-ms-transition-duration: 0.3s;
	-o-transition-duration: 0.3s;
}

.nav.navbar-left-sidebar .second-level .dropdown .third-level {
	opacity: 0 !important;
	display: block !important;
	visibility: hidden;
	left: 525px;
}

.nav.navbar-left-sidebar .second-level .dropdown.on .third-level {
	opacity: 1 !important;
	left: 569px;
	visibility: visible;
	transition-duration: 0.3s;
	-webkit-transition-duration: 0.3s;
	-moz-transition-duration: 0.3s;
	-ms-transition-duration: 0.3s;
	-o-transition-duration: 0.3s;
}

.nav.navbar-left-sidebar .dropdown-menu .dropdown > a:after {
	display: none
}

/* pull left sidebar */
.cbp-spmenu {
	background: $white;
	position: fixed;
	z-index: 1;
}

.cbp-spmenu-vertical {
	width: 455px;
	height: 100%;
	top: 0;
	z-index: 10005;
}

.cbp-spmenu-right {
	right: -455px;
}

.cbp-spmenu-left {
	left: -100%;
}

.cbp-spmenu-left.cbp-spmenu-open {
	left: 0;
}

.cbp-spmenu-right.cbp-spmenu-open {
	right: 0px;
	overflow-x: auto;
	box-shadow: 0 0 4px rgba($black, .2);
}

.cbp-spmenu-push {
	overflow-x: hidden;
	position: relative;
	left: 0;
}

.cbp-spmenu, .cbp-spmenu-push {
	-webkit-transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

/* custome scrollbar */
.cbp-spmenu-right::-webkit-scrollbar, .navbar-left-sidebar::-webkit-scrollbar {
	width: 5px;
}

.cbp-spmenu-right::-webkit-scrollbar-track, .navbar-left-sidebar::-webkit-scrollbar-track {
	box-shadow: inset 0 0 6px rgba($black, 0.3);
	-webkit-box-shadow: inset 0 0 6px rgba($black, 0.3);
	-moz-box-shadow: inset 0 0 6px rgba($black, 0.3);
	-ms-box-shadow: inset 0 0 6px rgba($black, 0.3);
	-o-box-shadow: inset 0 0 6px rgba($black, 0.3);
}

.cbp-spmenu-right::-webkit-scrollbar-thumb, .navbar-left-sidebar::-webkit-scrollbar-thumb {
	background-color: darkgrey;
	outline: 1px solid slategrey;
}

/* sidebar navigation */
nav.navbar.sidebar-nav .container {
	display: table-cell;
	height: 75px;
