}

.sidebar-nav-style-1 .dropdown .third-level {
	top: 0;
	left: 570px;
	width: 280px;
	background: #f3f3f3;
}

.sidebar-nav-style-1 .dropdown ul.second-level li ul {
	display: none;
	opacity: 0
}

/* sidebar navigation style 2 */
.left-nav-sidebar {
	padding-left: 90px;
	transition: all 0.3s ease 0s;
}

.left-nav {
	position: fixed;
	top: 0;
	width: auto;
	left: 0;
	height: 100%;
	background-color: $white;
	z-index: 99999;
	transition: all 0.3s ease 0s
}

.sidebar-part1 {
	position: absolute;
	top: 0;
	width: 90px;
}

.sidebar-part2 {
	padding: 0 0 0 110px;
	border-right: 1px solid #dfdfdf;
	width: 400px;
	height: 100%;
	position: fixed;
	background-color: $white;
	z-index: -1;
	left: -370px;
}

.sidebar-part3 {
