}

header .sidebar-part2 nav.navbar.bootsnav ul li ul li {
	margin: 0;
	padding: 0 30px;
	width: 100%;
	word-break: normal;
}

header .sidebar-part2 nav.navbar.bootsnav ul li ul li.dropdown {
