	-o-box-shadow: none;
	-ms-box-shadow: none;
	margin-right: 0;
	margin-left: -1px;
	text-transform: uppercase;
	border-width: 1px;
	border-style: solid;
	padding: 5px 9px;
	height: 31px
}

.search-box .add-on .form-control:focus, .search-box .add-on .input-group-btn > .btn:focus, .search-box .add-on .input-group-btn > .btn:hover {
