/* list style 1 */
.list-style-none li {
	list-style: none;
}

.list-style-1 li {
	padding: 5px 0
}

/* list style 2 */
.list-style-2 {
	list-style: none;
	margin: 0;
	padding: 0;
}

.list-style-2 li {
	position: relative;
	padding: 0 0 0 12px;
	margin: 0 0 9px 0;
}

.list-style-2 li:before {
	content: "";
	width: 5px;
	height: 1px;
	background-color: $primary;
	position: absolute;
	top: 12px;
	left: 0;
}

/* list style 3 */
.list-style-3 {
	list-style: none;
	margin: 0;
	padding: 0;
}

.list-style-3 li {
	position: relative;
	padding: 0 0 0 22px;
	margin: 0 0 15px 0;
}

.list-style-3 li:before {
	content: "";
	width: 10px;
	height: 1px;
	background-color: $primary;
	position: absolute;
	top: 13px;
	left: 0;
}

/* list style 4 */
