#rev_slider_1 {
  color: $white;
  text-align: center;
	
	&.text-left,
	.text-left {
		text-align: left;
	}
  
  .hidden {
    display: none;
  }
	
  .offset-top-30 {
    margin-top: 30px;
  }
  
  .offset-top-20 {
    margin-top: 20px;
  }
  
  @media (min-width: 768px) {
    .reveal-sm-block {
      display: block !important;
    }
  }
  
  h4.text-light {
    font-size: 23px;
    line-height: 1.4;
  }
  
  .group {
    margin-left: -15px;
    > * {
      margin-left: 15px;
			margin-top: 10px;
    }
  }
  
  .btn:not(button) {
    padding: 17px 24px;
    border-radius: 30px;
		font-size: 12px;
  }
  
  .btn-primary {
    &, &:focus, &:active {
      color: $white;
      background-color: $primary;
      border-color: $primary;
    }
    &:hover {
      color: $white;
      background-color: transparent;
      border-color: $white;
    }
  }
  
  .btn-default {
    &, &:focus, &:active {
      color: $gray-900;
      background-color: $white;
      border-color: $white;
    }
    &:hover {
      color: $white;
      background-color: transparent;
      border-color: $white;
    }
  }
  
  .shell {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto
  }
  
  @media (min-width: 768px) {
    .shell {
      max-width: 750px
    }
  }
  
  @media (min-width: 992px) {
    .shell {
      max-width: 970px
    }
  }
  
  @media (min-width: 1200px) {
    .shell {
      max-width: 970px
    }
  }
  
  .range {
    margin-right:-15px;
    margin-left:-15px
  }
  
  .cell-lg-10 {
    width: 83.33333333% !important;
    margin-left:8.33333333% !important;
  }
}


