/**
*
* RD Navbar Classic
* =====================================================
*/
.rd-navbar-classic {
  // Static
  &.rd-navbar-static {
    border-bottom: 1px solid transparent;
    
    .rd-navbar-aside-outer,
    .rd-navbar-main-outer {
      padding-left: 15px;
      padding-right: 15px;
    }
  
    .rd-navbar-aside,
    .rd-navbar-main {
      max-width: $rd-navbar-static-width;
      margin-left: auto;
      margin-right: auto;
    }
    
    .rd-navbar-aside {
      padding: 10px 0;
    }
  
    .rd-navbar-main {
      // position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px 0;
    }
  
    // RD Navbar Brand
    .rd-navbar-brand {
      img {
        width: auto;
        height: auto;
        max-width: 200px;
        max-height: 200px;
      }
    }
  
    .rd-navbar-main-element {
      display: flex;
      align-items: center;
    }
  
    &.rd-navbar--is-stuck,
    &.rd-navbar--is-clone {
      border-bottom-color: nth($rd-navbar-border, 3);
      .rd-navbar-aside-outer {
        display: none;
      }
    
      .rd-navbar-main {
        padding: 17px 0;
      }
    
      .rd-navbar-nav-item {
        > .rd-navbar-submenu {
          margin-top: 17px;
        }
      }
    }
  }
  
  // Fixed 
  &.rd-navbar-fixed {
  
  }
}