/* ==============================================================

Template name : Bootsnav - Multi Purpose Header
Categorie : Bootstrap Menu in CSS
Author : adamnurdin01
Version : v.1.2
Created : 2016-06-02
Last update : 2016-10-19

============================================================== */

nav.bootsnav .megamenu-fw .dropdown-menu {
	left: auto;
}

nav.bootsnav .megamenu-content {
	padding: 15px;
	width: 100%;
}

nav.bootsnav .megamenu-content .title {
	margin-top: 0;
}

nav.bootsnav .dropdown.megamenu-fw .dropdown-menu {
	left: 0;
	right: 0;
}

/* navbar */
nav.navbar.bootsnav {
	margin-bottom: 0;
	-moz-border-radius: 0px;
	-webkit-border-radius: 0px;
	-o-border-radius: 0px;
	border-radius: 0px;
	border: none;
	// border-bottom: solid 1px $gray-600;
}

nav.navbar.bootsnav ul.nav > li > a {
	color: #6f6f6f;
	background-color: transparent !important;
}

nav.navbar.bootsnav ul.nav li.megamenu-fw > a:hover, nav.navbar.bootsnav ul.nav li.megamenu-fw > a:focus, nav.navbar.bootsnav ul.nav li.active > a:hover, nav.navbar.bootsnav ul.nav li.active > a:focus, nav.navbar.bootsnav ul.nav li.active > a {
	background-color: transparent;
}

nav.navbar.bootsnav .navbar-toggle {
	background-color: transparent !important;
	border: none;
	padding: 0;
	font-size: 18px;
	position: relative;
	top: 5px;
}

nav.navbar.bootsnav ul.nav ul.dropdown-menu > li > a {
	white-space: normal;
}

ul.menu-col {
	padding: 0;
	margin: 0;
	list-style: none;
}

ul.menu-col li a {
	color: #6f6f6f;
}

ul.menu-col li a:hover, ul.menu-col li a:focus {
	text-decoration: none;
}

/* navbar full */
nav.bootsnav.navbar-full {
	padding-bottom: 10px;
	padding-top: 10px;
}

nav.bootsnav.navbar-full .navbar-header {
	display: block;
}

nav.bootsnav.navbar-full .navbar-toggle {
	display: inline-block;
	margin-right: 0;
	position: relative;
	top: 0;
	font-size: 30px;
	-webkit-transition: all 1s ease-in-out;
	-moz-transition: all 1s ease-in-out;
	-o-transition: all 1s ease-in-out;
	-ms-transition: all 1s ease-in-out;
	transition: all 1s ease-in-out;
	margin: 0;
}

nav.bootsnav.navbar-full .navbar-toggle:hover, nav.bootsnav.navbar-full .navbar-toggle:focus {
	background-color: inherit;
}

nav.bootsnav.navbar-full .navbar-collapse {
	position: fixed;
	width: 100%;
	height: 100% !important;
	top: 0;
	left: 0;
	padding: 0;
	display: none !important;
	z-index: 9;
	max-height: 100% !important
}

nav.bootsnav.navbar-full .navbar-collapse.in {
	display: block !important;
}

nav.bootsnav.navbar-full .navbar-collapse .nav-full {
	overflow: auto;
}

nav.bootsnav.navbar-full .navbar-collapse .wrap-full-menu {
	display: table-cell;
	vertical-align: middle;
	overflow: auto;
	background: rgba(0, 0, 0, .88)
}

nav.bootsnav.navbar-full .navbar-collapse .nav-full::-webkit-scrollbar {
	width: 0;
}

nav.bootsnav.navbar-full .navbar-collapse .nav-full::-moz-scrollbar {
	width: 0;
}

nav.bootsnav.navbar-full .navbar-collapse .nav-full::-ms-scrollbar {
	width: 0;
}

nav.bootsnav.navbar-full .navbar-collapse .nav-full::-o-scrollbar {
	width: 0;
}

nav.bootsnav.navbar-full .navbar-collapse .nav {
	display: block;
	width: 100%;
}

nav.navbar.bootsnav.navbar-full li {
	margin: 20px 0
}

nav.navbar.bootsnav.navbar-full li a {
	font-size: 22px !important;
	color: #232323;
	font-weight: bold;
	line-height: 26px !important;
	background: transparent
}

nav.navbar.bootsnav.navbar-full li.dropdown ul {
	padding: 5px 15px !important
}

nav.navbar.bootsnav.navbar-full li.dropdown ul.dropdown-menu {
	top: 0px !important;
	left: auto;
	margin: 0;
	top: 10px;
	padding: 10px 20px;
	position: relative;
	float: none;
	background: inherit;
	border: 0;
	box-shadow: none;
}

nav.navbar.bootsnav.navbar-full li.dropdown ul.dropdown-menu li {
	margin: 5px 0;
}

nav.navbar.bootsnav.navbar-full li.dropdown ul.dropdown-menu li a {
	color: #232323 !important;
	border: none !important;
	padding: 0;
	font-size: 14px !important;
	font-weight: normal
}

nav.navbar.bootsnav.navbar-full li.dropdown ul.dropdown-menu li a:hover {
	color: $primary !important;
}

nav.navbar.bootsnav.navbar-full ul > li a {
	padding-left: 35px;
	position: relative;
}

nav.navbar.bootsnav.navbar-full ul > li a:hover, nav.navbar.bootsnav.navbar-full ul li.dropdown.open a {
	color: $primary;
}

nav.navbar.bootsnav.navbar-full ul > li a:hover:after, nav.navbar.bootsnav.navbar-full ul li.dropdown.open a:after {
	background: $primary
}

nav.navbar.bootsnav.navbar-full ul > li a::after {
	background: $gray-200 none repeat scroll 0 0;
	content: "";
	display: inline-block;
	height: 4px;
	left: 0;
	position: absolute;
	top: 14px;
	width: 18px;
}

nav.navbar.bootsnav.navbar-full ul > li.dropdown ul.dropdown-menu li a:after {
	display: none
}

nav.navbar.bootsnav.navbar-full ul > li.dropdown ul.dropdown-menu {
	padding-left: 50px !important
}

.close-full-menu {
	background: $black none repeat scroll 0 0;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 78
}

.close-full-menu > a {
	float: left;
	padding: 10px 15px;
}

/* atribute navigation */
.attr-nav {
	float: right;
	display: inline-block;
	margin-left: 13px;
	margin-right: -15px;
}

.attr-nav > ul {
	padding: 0;
	margin: 0 0 -7px 0;
	list-style: none;
	display: inline-block;
}

.attr-nav > ul > li {
	float: left;
	display: block;
}

.attr-nav > ul > li > a {
	color: #6f6f6f;
	display: block;
	padding: 28px 15px;
	position: relative;
}

.attr-nav > ul > li > a span.badge {
	position: absolute;
	top: 50%;
	margin-top: -15px;
	right: 5px;
	font-size: 10px;
	padding: 0;
	width: 15px;
	height: 15px;
	padding-top: 2px;
}

.attr-nav > ul > li.dropdown ul.dropdown-menu {
	-moz-border-radius: 0px;
	-webkit-border-radius: 0px;
	-o-border-radius: 0px;
	border-radius: 0px;
	border: solid 1px $gray-600;
}

ul.cart-list {
	padding: 0 !important;
	width: 250px !important;
}

ul.cart-list > li {
	position: relative;
	border-bottom: solid 1px #efefef;
	padding: 15px 15px 23px 15px !important;
}

ul.cart-list > li > a.photo {
	padding: 0 !important;
	margin-right: 15px;
	float: left;
	display: block;
	width: 50px;
	height: 50px;
	left: 15px;
	top: 15px;
}

ul.cart-list > li img {
	width: 50px;
	height: 50px;
	border: solid 1px #efefef;
}

ul.cart-list > li > h6 {
	margin: 0;
}

ul.cart-list > li > h6 > a.photo {
	padding: 0 !important;
	display: block;
}

ul.cart-list > li > p {
	margin-bottom: 0;
}

ul.cart-list > li.total {
	background-color: #f5f5f5;
	padding-bottom: 15px !important;
}

ul.cart-list > li.total > .btn {
	display: inline-block;
	border-bottom: solid 1px #efefef !important;
}

ul.cart-list > li .price {
	font-weight: bold;
}

ul.cart-list > li.total > span {
	padding-top: 8px;
}

/* top search */
.top-search {
	background-color: #333;
	padding: 10px 0;
	display: none;
}

.top-search input.form-control {
	background-color: transparent;
	border: none;
	color: $white;
	height: 40px;
	padding: 0 15px;
}

.top-search .input-group-addon {
	background-color: transparent;
	border: none;
	color: $white;
	padding-left: 0;
	padding-right: 0;
}

.top-search .input-group-addon.close-search {
	cursor: pointer;
}

/* side menu */
body {
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

body.on-side {
	margin-left: -280px;
}

.side {
	position: fixed;
	overflow-y: auto;
	top: 0;
	right: -280px;
	width: 280px;
	padding: 25px 30px;
	height: 100%;
	display: block;
	background-color: #333;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	z-index: 9;
}

.side.on {
	right: 0;
}

.side .close-side {
	float: right;
	color: $white;
	position: relative;
	z-index: 2;
	font-size: 16px;
}

.side .widget {
	position: relative;
	z-index: 1;
	margin-bottom: 25px;
}

.side .widget .title {
	color: $white;
	margin-bottom: 15px;
}

.side .widget ul.link {
	padding: 0;
	margin: 0;
	list-style: none;
}

.side .widget ul.link li a {
	color: #9f9f9f;
	letter-spacing: 1px;
}

.side .widget ul.link li a:focus,
.side .widget ul.link li a:hover {
	color: $white;
	text-decoration: none;
}

/* share */
nav.navbar.bootsnav .share {
	padding: 0 30px;
	margin-bottom: 30px;
}

nav.navbar.bootsnav .share ul {
	display: inline-block;
	padding: 0;
	margin: 0 0 -7px 0;
	list-style: none;
}

nav.navbar.bootsnav .share ul > li {
	float: left;
	display: block;
	margin-right: 5px;
}

nav.navbar.bootsnav .share ul > li > a {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	width: 35px;
	height: 35px;
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	-o-border-radius: 50%;
	border-radius: 50%;
	background-color: #cfcfcf;
	color: $white;
}

/* transparent */
nav.navbar.bootsnav.navbar-fixed {
	position: fixed;
	display: block;
	width: 100%;
}

nav.navbar.bootsnav.no-background {
	-webkit-transition: all 1s ease-in-out;
	-moz-transition: all 1s ease-in-out;
	-o-transition: all 1s ease-in-out;
	-ms-transition: all 1s ease-in-out;
	transition: all 1s ease-in-out;
}

/* navbar sticky */
.wrap-sticky {
	position: relative;
	-webkit-transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.wrap-sticky nav.navbar.bootsnav {
	position: absolute;
	width: 100%;
	left: 0;
	top: 0;
}

.wrap-sticky nav.navbar.bootsnav.sticked {
	position: fixed;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	-ms-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;
}

body.on-side .wrap-sticky nav.navbar.bootsnav.sticked {
	left: -280px;
}

@media (min-width: 1024px) and (max-width: 1400px) {

	body.wrap-nav-sidebar .wrapper .container {
		width: 100%;
		padding-left: 30px;
	}
}

@media (min-width: 1024px) {

	/* general navbar */
	nav.navbar.bootsnav ul.nav > li > a {
		padding: 15px 5px;
		@media (min-width: 1300px) {
			padding: 30px 15px;
		}
	}
	nav.navbar.bootsnav ul.nav > li.dropdown > a.dropdown-toggle:after {
		font-family: 'FontAwesome';
		content: "\f0d7";
		margin-left: 5px;
		margin-top: 2px;
	}
	nav.navbar.bootsnav li.dropdown ul.dropdown-menu {
		-moz-border-radius: 0px;
		-webkit-border-radius: 0px;
		-o-border-radius: 0px;
		border-radius: 0px;
		border: none;
	}
	nav.navbar.bootsnav li.dropdown ul.dropdown-menu > li a:hover,
	nav.navbar.bootsnav li.dropdown ul.dropdown-menu > li:last-child > a {
		border-bottom: none;
	}
	nav.navbar.bootsnav ul.navbar-right li.dropdown ul.dropdown-menu li a {
		text-align: right;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content.tabbed {
		padding: 0;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content.tabbed > li {
		padding: 0;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content .col-menu {
		padding: 0 30px;
		margin: 0 -0.5px;
		border-left: solid 1px #f0f0f0;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content .col-menu:first-child {
		border-left: none;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content .col-menu:last-child {
		border-right: none;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content .content {
		display: none;
	}
	nav.navbar.bootsnav ul.dropdown-menu.megamenu-content .content ul.menu-col li a {
		text-align: left;
		padding: 5px 0;
		display: block;
		width: 100%;
		margin-bottom: 0;
		border-bottom: none;
		color: #6f6f6f;
	}
	nav.navbar.bootsnav.on ul.dropdown-menu.megamenu-content .content {
		display: block !important;
		height: auto !important;
	}

	/* navbar transparent */
	nav.navbar.bootsnav.no-background {
		background-color: transparent;
		border: none;
	}
	nav.navbar.bootsnav.navbar-transparent .attr-nav {
		padding-left: 15px;
		margin-left: 30px;
	}

	nav.navbar.bootsnav.navbar-transparent.white {
		background-color: rgba(255, 255, 255, 0.3);
		border-bottom: solid 1px #bbb;
	}
	nav.navbar.navbar-inverse.bootsnav.navbar-transparent.dark, nav.navbar.bootsnav.navbar-transparent.dark {
		background-color: rgba(0, 0, 0, 0.3);
		border-bottom: solid 1px #555;
	}
	nav.navbar.bootsnav.navbar-transparent.white .attr-nav {
		border-left: solid 1px #bbb;
	}
	nav.navbar.navbar-inverse.bootsnav.navbar-transparent.dark .attr-nav, nav.navbar.bootsnav.navbar-transparent.dark .attr-nav {
		border-left: solid 1px #555;
	}
	nav.navbar.bootsnav.no-background.white .attr-nav > ul > li > a, nav.navbar.bootsnav.navbar-transparent.white .attr-nav > ul > li > a, nav.navbar.bootsnav.navbar-transparent.white ul.nav > li > a, nav.navbar.bootsnav.no-background.white ul.nav > li > a {
		color: $white;
	}
	nav.navbar.bootsnav.navbar-fixed.navbar-transparent .logo-scrolled, nav.navbar.bootsnav.navbar-fixed.no-background .logo-scrolled {
		display: none;
	}
	nav.navbar.bootsnav.navbar-fixed.navbar-transparent .logo-display, nav.navbar.bootsnav.navbar-fixed.no-background .logo-display {
		display: block;
	}
	nav.navbar.bootsnav.navbar-fixed .logo-display {
		display: none;
	}
	nav.navbar.bootsnav.navbar-fixed .logo-scrolled {
		display: block;
	}

	/* atribute navigation */
	.attr-nav > ul > li.dropdown ul.dropdown-menu {
		margin-top: 0;
		margin-left: 55px;
		width: 250px;
		left: -250px;
	}

	/* menu center */
	nav.navbar.bootsnav.menu-center .container {
		position: relative;
	}
	nav.navbar.bootsnav.menu-center ul.nav.navbar-center {
		float: none;
		margin: 0 auto;
		display: table;
		table-layout: fixed;
	}
	nav.navbar.bootsnav.menu-center .navbar-header, nav.navbar.bootsnav.menu-center .attr-nav {
		position: absolute;
	}
	nav.navbar.bootsnav.menu-center .attr-nav {
		right: 15px;
	}

	/* navbar brand top */
	nav.bootsnav.navbar-brand-top .navbar-header {
		display: block;
		width: 100%;
		text-align: center;
	}
	nav.bootsnav.navbar-brand-top ul.nav > li.dropdown > ul.dropdown-menu {
		margin-top: 2px;
	}
	nav.bootsnav.navbar-brand-top ul.nav > li.dropdown.megamenu-fw > ul.dropdown-menu {
		margin-top: 0;
	}
	nav.bootsnav.navbar-brand-top .navbar-header .navbar-brand {
		display: inline-block;
		float: none;
		margin: 0;
	}
	nav.bootsnav.navbar-brand-top .navbar-collapse {
		text-align: center;
	}
	nav.bootsnav.navbar-brand-top ul.nav {
		display: inline-block;
		float: none;
		margin: 0 0 -5px 0;
	}

	/* navbar center */
	nav.bootsnav.brand-center .navbar-header {
		display: block;
		width: 100%;
		position: absolute;
		text-align: center;
		top: 0;
		left: 0;
	}
	nav.bootsnav.brand-center .navbar-brand {
		display: inline-block;
		float: none;
	}
	nav.bootsnav.brand-center .navbar-collapse {
		text-align: center;
		display: inline-block;
		padding-left: 0;
		padding-right: 0;
	}
	nav.bootsnav.brand-center ul.nav > li.dropdown > ul.dropdown-menu {
		margin-top: 2px;
	}
	nav.bootsnav.brand-center ul.nav > li.dropdown.megamenu-fw > ul.dropdown-menu {
		margin-top: 0;
	}
	nav.bootsnav.brand-center .navbar-collapse .col-half {
		width: 50%;
		float: left;
		display: block;
	}
	nav.bootsnav.brand-center .navbar-collapse .col-half.left {
		text-align: right;
		padding-right: 100px;
	}
	nav.bootsnav.brand-center .navbar-collapse .col-half.right {
		text-align: left;
		padding-left: 100px;
	}
	nav.bootsnav.brand-center ul.nav {
		float: none !important;
		margin-bottom: -5px !important;
		display: inline-block !important;
	}
	nav.bootsnav.brand-center ul.nav.navbar-right {
		margin: 0;
	}
	nav.bootsnav.brand-center.center-side .navbar-collapse .col-half.left {
		text-align: left;
		padding-right: 100px;
	}
	nav.bootsnav.brand-center.center-side .navbar-collapse .col-half.right {
		text-align: right;
		padding-left: 100px;
	}

	/*  navbar sidebar */
	body.wrap-nav-sidebar .wrapper {
		padding-left: 260px;
		overflow-x: hidden;
	}
	nav.bootsnav.navbar-sidebar {
		position: fixed;
		width: 260px;
		overflow: hidden;
		left: 0;
		padding: 0 0 0 0 !important;
		background: $white;
	}
	nav.bootsnav.navbar-sidebar .scroller {
		width: 280px;
		overflow-y: auto;
		overflow-x: hidden;
	}
	nav.bootsnav.navbar-sidebar .container-fluid, nav.bootsnav.navbar-sidebar .container {
		padding: 0 !important;
	}
	nav.bootsnav.navbar-sidebar .navbar-header {
		float: none;
		display: block;
		width: 260px;
		padding: 10px 15px;
		margin: 10px 0 0 0 !important;
	}
	nav.bootsnav.navbar-sidebar .navbar-collapse {
		padding: 0 !important;
		width: 260px;
	}
	nav.bootsnav.navbar-sidebar ul.nav {
		float: none;
		display: block;
		width: 100%;
		padding: 0 15px !important;
		margin: 0 0 30px 0;
	}
	nav.bootsnav.navbar-sidebar ul.nav li {
		float: none !important;
	}
	nav.bootsnav.navbar-sidebar ul.nav > li > a {
		padding: 10px 15px;
		font-weight: bold;
	}
	nav.bootsnav.navbar-sidebar ul.nav > li.dropdown > a:after {
		float: right;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown ul.dropdown-menu {
		left: 100%;
		top: 0;
		position: relative !important;
		left: 0 !important;
		width: 100% !important;
		height: auto !important;
		background-color: transparent;
		border: none !important;
		padding: 0;
	}
	nav.bootsnav.navbar-sidebar ul.nav .megamenu-content .col-menu {
		border: none !important;
	}
	nav.bootsnav.navbar-sidebar ul.nav > li.dropdown > ul.dropdown-menu {
		margin-bottom: 15px;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown ul.dropdown-menu {
		padding-left: 0;
		float: none;
		margin-bottom: 0;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown ul.dropdown-menu li a {
		padding: 5px 15px;
		color: #6f6f6f;
		border: none;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown ul.dropdown-menu ul.dropdown-menu {
		padding-left: 15px;
		margin-top: 0;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown ul.dropdown-menu li.dropdown > a:before {
		font-family: 'FontAwesome';
		content: "\f105";
		float: right;
	}
	nav.bootsnav.navbar-sidebar ul.nav li.dropdown.on ul.dropdown-menu li.dropdown.on > a:before {
		content: "\f107";
	}
	nav.bootsnav.navbar-sidebar ul.dropdown-menu.megamenu-content > li {
		padding: 0 !important;
	}
	nav.bootsnav.navbar-sidebar .dropdown .megamenu-content .col-menu {
		display: block;
		float: none !important;
		padding: 0;
		margin: 0;
		width: 100%;
	}
	nav.bootsnav.navbar-sidebar .dropdown .megamenu-content .col-menu .title {
		padding: 7px 0;
		text-transform: none;
		font-weight: 400;
		letter-spacing: 0px;
		margin-bottom: 0;
		cursor: pointer;
		color: #6f6f6f;
	}
	nav.bootsnav.navbar-sidebar .dropdown .megamenu-content .col-menu .title:before {
		font-family: 'FontAwesome';
		content: "\f105";
		float: right;
