/*
*
* Snackbars
*/

.snackbars {
	padding: 9px 16px;
	margin-left: auto;
	margin-right: auto;
	color: $white;
	text-align: left;
	background-color: $gray-800;
	border-radius: 0;
	box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.15);
	
	font-size: 14px;
	
	p {
		margin: 0;
	}
	
	.icon-xxs {
		font-size: 18px;
	}
	
	// padding on text
	p span:last-child {
		padding-left: 14px;
	}
	
	// snackbar icon on the left side
	&-left {
		display: inline-block;
		margin-bottom: 0;
	}
	
	// snackbar link or button on the right side
	&-right {
		display: inline-block;
		float: right;
		text-transform: uppercase;
		
		&:hover {
			text-decoration: underline;
		}
	}
	
	@media (min-width: 480px) {
		max-width: 540px;
		padding: 12px 15px;
		font-size: 15px;
	}
}
